/* 0. Reset ================================= */
@import 'reset';
@import 'font';






$mobile-break: 320px;
$tablet-break: 768px;
$desktop-break: 1200px;


$bandfont: 'viimeinen_syksyregular';

$accentRed: #de4344;
$darkRed: #b13637;
$accentBlue: #65a6af;

// /* 1. Main Layout ================================= */


* {
	box-sizing: border-box;
}

/* 2. Loader ================================= */

.loader {
	position: relative;
	width: 100%;
	min-height: 100vh;
	display: none;

		.top-text-wrapper {
			position: absolute;
			height: 10vh;
			top: 0;
			font-family: $bandfont;
			font-size: 10vh;
			display: flex;
			flex-direction: row;
			justify-content: center;

			.bolt1-loader, .bolt2-loader {
				display: inline;
				width: 5%;
			}

			.bolt1 {
				transform: rotate(45deg);
			}

			.bolt2 {
				transform: rotate(-90deg);
			}
		}


	.loader-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100vh;

		.speaker-loader, .logo-loader {
			padding: 10px;
			width: 33%;
		}
		.speaker-loader {
			opacity: 1;
		}
		.logo-loader {
		 opacity: 0;	
		}
	}

}

